"use client";

import React from "react";
import { cn } from "@/lib/utils";

interface LogoProps {
    className?: string;
    withText?: boolean;
}

export const Logo = ({ className, withText = true }: LogoProps) => {
    return (
        <div className={cn("flex items-center gap-3", className)}>
            {/* Logo Symbol */}
            <div className="relative w-10 h-10 flex items-center justify-center">
                {/* Rounded Square Outline */}
                <div className="absolute inset-0 border-2 border-white rounded-lg" />

                {/* Code Brackets */}
                <span className="font-mono text-white font-bold text-sm relative z-10 tracking-tighter">
                    {"< />"}
                </span>

                {/* Solid Red Triangle (Dropdown Arrow) */}
                {/* Placing it in the bottom-right corner of the border */}
                <div
                    className="absolute -bottom-1 -right-1 w-0 h-0 border-l-[8px] border-l-transparent border-t-[8px] border-t-transparent border-r-[8px] border-r-[#FF1E1E] border-b-[8px] border-b-[#FF1E1E] rotate-0"
                    style={{
                        clipPath: "polygon(100% 0, 0% 100%, 100% 100%)"
                    }}
                />
                {/* Alternative Triangle approach for cleaner rendering if clip-path is tricky: 
            Just a small red square masking? No, let's use SVG for precision if div is messy. 
            Let's stick to SVG for the whole symbol to be safe and crisp.
        */}
            </div>
        </div>
    );
};


export const LogoSVG = ({ className, withText = true }: LogoProps) => {
    return (
        <div className={cn("flex items-center gap-3 select-none", className)}>
            <div className="relative w-10 h-10">
                <svg width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg">
                    {/* Rounded Square Border */}
                    <rect x="2" y="2" width="36" height="36" rx="8" stroke="white" strokeWidth="2.5" />

                    {/* Code Brackets */}
                    <path d="M14 15L9 20L14 25" stroke="white" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" />
                    <path d="M26 15L31 20L26 25" stroke="white" strokeWidth="2.5" strokeLinecap="round" strokeLinejoin="round" />
                    <path d="M21 13L19 28" stroke="white" strokeWidth="2.5" strokeLinecap="round" />

                    {/* Red Triangle - Bottom Right */}
                    {/* Positioned to overlap the corner */}
                    <path d="M28 28H39V40L28 28Z" fill="#FF1E1E" />
                    {/* Wait, standard triangle geometry: Bottom right corner. 
                        Let's make it look like a "tab" or overlay. 
                        User said: "Solid Red Triangle... in the bottom-right corner of the border" 
                    */}
                    <path d="M26 40H40V26L26 40Z" fill="#FF1E1E" />
                </svg>
            </div>

            {withText && (
                <div className="flex flex-col">
                    <span className="font-bold text-white text-lg leading-none tracking-tight">DROPDOWN</span>
                    <span className="text-[#FF1E1E] text-xs font-bold tracking-[0.2em] leading-none">DIGITAL</span>
                </div>
            )}
        </div>
    )
}
